'use strict';

import q from 'q';
import url from 'url'
import request from 'request';

class Bitly {
  constructor(clientId, clientSecret, accessToken, options) {
    this.clientId = clientId;
    this.clientSecret = clientSecret;
    this.accessToken = accessToken;
    this.options = options || {
        format: 'json',
        api_url: 'api-ssl.bit.ly',
        api_version: 'v3',
        domain: 'bit.ly'
      };
  }


}

export default Bitly;